@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;  
}

body, h1, h2, h3, h4, h5, h6 {
  font-family: 'Open Sans Condensed', sans-serif;
}

h1 {
  font-size: 32px;
  margin-bottom: 30px;
}

.app {
  color: #050505;
  margin: 0 auto;
  width: 68%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.app hr {
  padding: 0;
  margin: 0 0 30px 0;
  display: block;
}

.app button {
  display: block;
  padding: 10px 20px;
  font-size: 10px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}


.app__header {
  text-align: center;
  min-height: 35vh;
  background-color: #e9e9e9;
}

.app__main {
  display: flex;
  flex-flow: row wrap;
  flex-grow: 1;
}

.app__nav {
  width: 20%;
}

.app__page {
  flex-grow: 1;
  width: 80%;
}

.app__nav,
.app__page {
  padding: 30px 0;
}

.app__footer {
  text-align: center;
  padding: 30px;
  text-transform: uppercase;
  letter-spacing: 0.5em;
  background-color: #e9e9e9;
}
.app__header {
    position: relative;
    overflow: hidden
}

.app__header img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
}
.app__nav ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.app__nav ul a {
    color: #050505;
    display: block;
    padding: 5px 20px;
    -webkit-transition: -webkit-transform 0.15s ease;
    transition: -webkit-transform 0.15s ease;
    transition: transform 0.15s ease;
    transition: transform 0.15s ease, -webkit-transform 0.15s ease;
    text-decoration: none;
    position: relative;
    overflow: hidden;
}

.app__nav ul a:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 5px;
    background-color: #000;
    opacity: 0;
    -webkit-transform: translate(0px, -100%);
            transform: translate(0px, -100%);
    -webkit-transition: opacity 0.15s ease-in-out, -webkit-transform 0.15s ease-in-out;
    transition: opacity 0.15s ease-in-out, -webkit-transform 0.15s ease-in-out;
    transition: transform 0.15s ease-in-out, opacity 0.15s ease-in-out;
    transition: transform 0.15s ease-in-out, opacity 0.15s ease-in-out, -webkit-transform 0.15s ease-in-out;
}

.app__nav ul li:nth-child(1) a:before {
    background-color: blue;
}

.app__nav ul li:nth-child(2) a:before {
    background-color: red;
}

.app__nav ul li:nth-child(3) a:before {
    background-color: green;
}

.app__nav ul li:nth-child(4) a:before {
    background-color: orange;
}

.app__nav ul a:not(.active):hover {
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
}

.app__nav ul a.active {
    color: crimson;
}

.app__nav ul a.active:before {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
}
.app__products ul {
    padding-left: 20px;
    list-style: square;
    font-size: 18px;
}

.app__products ul li:not(:last-child) {
    margin-bottom: 10px;
}

.app__products a {
    color: #333;
    text-decoration: none;
}

.app__products a:hover {
    color: crimson;
    text-decoration: underline;
}
.app__form-text {
    margin-bottom: 20px;
    display: block;
    width: 100%;
    min-height: 100px;
    padding: 20px;
    font-size: 14px;
    line-break: 1.55;
}
.app__form-login input {
    padding: 10px 20px;
    font-size: 14px;
    margin-bottom: 30px;
    display: block;
}
.app__footer p {
    font-size: 16px;
    letter-spacing: normal;
    text-transform: none;
}
